import React, { useState, useEffect } from "react";
import { Layout } from "../../components/Layout";
import axios from "axios";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { showLoading, hideLoading } from "../../redux/features/alertSlice";
import { toast } from "react-toastify";

const SingleClubUser = () => {
  const params = useParams();
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.user);

  useEffect(() => {
    console.log("User object:", user);
    console.log("User identifier:", user?.identifier);
    console.log("User name:", user?.name);
  }, [user]);

  const [club, setClub] = useState({
    clubId: "",
    name: "",
    clubType: "",
    img: [],
    clubInfo: "",
    resources: "",
    applicationForm: "",
    date: "",
  });

  const getClubInfo = async () => {
    if (!user?.identifier || !user?.name) {
      console.log("User identifier or name is not available");
      return;
    }

    try {
      const res = await axios.post(
        "/api/v1/admin/get-club-info",
        { clubId: params.id },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        setClub(res.data.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getClubInfo();
  }, [params.id, user]);

  const handleApply = async () => {
    const confirmation = window.confirm(
      "Are you sure to apply for the club? Make sure you also filled the application form with your resume/projects."
    );
    if (confirmation) {
      try {
        dispatch(showLoading());
        const res = await axios.post(
          "/api/v1/user/apply-club",
          {
            clubId: params.id,
            identifier: user.identifier,
            name: user.name,
            clubName: club.name,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        dispatch(hideLoading());
        if (res.data.success) {
          toast.success(res.data.message);
        }
      } catch (error) {
        dispatch(hideLoading());
        console.log(error);
        toast.error("Error applying for the club");
      }
    }
  };

  return (
    <Layout>
      <div className="min-h-screen bg-gray-900 text-white p-6">
        <h1 className="text-3xl font-bold mb-4 text-center">Club Details</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {club.img.map((image, index) => (
            <div key={index} className="relative w-9/12 h-100 mb-4">
              <img
                src={image}
                alt={`Club ${index}`}
                className="w-full h-full object-cover rounded-md shadow-lg"
              />
            </div>
          ))}
        </div>
        <div className="mt-6 p-4 bg-gray-800 rounded-lg shadow-lg space-y-4">
          <div>
            <p className="text-xl font-semibold">
              Name of the Club: <span className="font-normal">{club.name}</span>
            </p>
          </div>
          <div>
            <p className="text-xl font-semibold">
              Club Type: <span className="font-normal">{club.clubType}</span>
            </p>
          </div>
          <div>
            <p className="text-xl font-semibold">Club Info:</p>
            <p className="text-gray-300">{club.clubInfo}</p>
          </div>
          <div>
            <p className="text-xl font-semibold">
              Resources: Follow the resources provided by the official club body
              to increase your chances of selection in the induction process.
            </p>
            <p className="text-gray-300">{club.resources}</p>
          </div>
          <div>
            <p className="text-xl font-semibold">Application Form:</p>
            <p className="text-gray-300">{club.applicationForm}</p>
          </div>
          <div>
            <p className="text-xl font-semibold">
              Date of Establishment:{" "}
              <span className="font-normal">
                {new Date(club.date).toLocaleDateString()}
              </span>
            </p>
          </div>
          <div className="flex justify-center mt-4">
            <button
              onClick={handleApply}
              className="p-2 bg-green-500 hover:bg-green-600 transition-colors duration-200 font-semibold text-black rounded-md"
            >
              Apply Now
            </button>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default SingleClubUser;
